.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background-color: #ffffff;
  color: rgb(0, 0, 0);
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 5px rgba(0, 0, 0, 0.125);
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}